import React, { Component } from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as yup from 'yup';
import { connect } from 'react-redux';
import * as actionsUser from '../../Redux/Actions/userAction';


const signupUserSchema = yup.object().shape({
    taiKhoan: yup.string().required('* Field is required!'),
    matKhau: yup.string().required('* Field is required!'),
    hoTen: yup.string().required('* Field is required!'),
    email: yup.string().required('* Field is required!').email('* Email không hợp lệ'),
    soDt: yup.string().required('* Field is required!').matches(/^[0-9]+$/, '* Số điện thoại không hợp lệ'),
    maNhom: yup.string().required('* Field is required!')
})

class Modal extends Component {
    handleSubmit = (values) => {
        let { infoModal } = this.props;
        console.log(values);

        // console.log(values);
        if (infoModal.addUser) {
        }
        this.props.addUser(values);
    }


    render() {
        let { infoModal, userEdit } = this.props;
        console.log(userEdit);
        console.log(infoModal);
        

        return (
            <div>
                <div className="modal fade" id="modalTask">
                    <div className="modal-dialog modal-lg">
                        <div className="modal-content">
                            {/* Modal Header */}
                            <div className="modal-header">
                                <h4 className="modal-title">
                                    {
                                        infoModal.addUser ? 'Thêm người dùng' :
                                            infoModal.editUser ? 'Sửa người dùng' :
                                                infoModal.addMovie ? 'Thêm phim' :
                                                    infoModal.editMovie ? 'Sửa phim' : ''
                                    }</h4>
                                <button type="button" className="close" data-dismiss="modal">×</button>
                            </div>
                            {/* Modal body */}
                            <div className="modal-body">
                                <Formik
                                    initialValues={{
                                        taiKhoan: '',
                                        email: '',
                                        soDt: '',
                                        hoTen: '',
                                        matKhau: '',
                                        maLoaiNguoiDung: 'KhachHang',
                                        maNhom: 'GP07'
                                    }}
                                    onSubmit={this.handleSubmit}
                                    validationSchema={signupUserSchema}
                                    render={(formikProps) => (
                                        <Form>
                                            <div className="form-group">
                                                <label>Tài khoản</label>
                                                <Field type="text" className="form-control" name='taiKhoan' onChange={formikProps.handleChange} />
                                                <ErrorMessage name="taiKhoan" >
                                                    {msg => <div className="alert alert-danger">{msg}</div>}
                                                </ErrorMessage>
                                            </div>
                                            <div className="form-group">
                                                <label>Họ tên</label>
                                                <Field type="text" className="form-control" name='hoTen' onChange={formikProps.handleChange} />
                                                <ErrorMessage name="hoTen" >
                                                    {msg => <div className="alert alert-danger">{msg}</div>}
                                                </ErrorMessage>
                                            </div>
                                            <div className="form-group">
                                                <label>Email</label>
                                                <Field type="email" className="form-control" name='email' onChange={formikProps.handleChange} />
                                                <ErrorMessage name="email" >
                                                    {msg => <div className="alert alert-danger">{msg}</div>}
                                                </ErrorMessage>
                                            </div>
                                            <div className="form-group">
                                                <label>Số điện thoại</label>
                                                <Field type="text" className="form-control" name='soDt' onChange={formikProps.handleChange} />
                                                <ErrorMessage name="soDt" >
                                                    {msg => <div className="alert alert-danger">{msg}</div>}
                                                </ErrorMessage>
                                            </div>
                                            <div className="form-group">
                                                <label>Mật khẩu</label>
                                                <Field type="password" className="form-control" name='matKhau' onChange={formikProps.handleChange} />
                                                <ErrorMessage name="matKhau" >
                                                    {msg => <div className="alert alert-danger">{msg}</div>}
                                                </ErrorMessage>
                                            </div>
                                            <div className="form-group">
                                                <label>Loại người dùng</label>
                                                <Field component="select" className="form-control" name='maLoaiNguoiDung' onChange={formikProps.handleChange}>
                                                    <option>KhachHang</option>
                                                    <option>QuanTri</option>
                                                </Field>
                                                <ErrorMessage name="maLoaiNguoiDung" >
                                                    {msg => <div className="alert alert-danger">{msg}</div>}
                                                </ErrorMessage>
                                            </div>
                                            <div className="form-group">
                                                <label>Mã nhóm</label>
                                                <Field component="select" className="form-control" name='maNhom' onChange={formikProps.handleChange}>
                                                    <option>GP01</option>
                                                    <option>GP02</option>
                                                    <option>GP03</option>
                                                    <option>GP04</option>
                                                    <option>GP05</option>
                                                    <option>GP06</option>
                                                    <option>GP07</option>
                                                    <option>GP08</option>
                                                    <option>GP09</option>
                                                    <option>GP10</option>
                                                </Field>
                                                <ErrorMessage name="maNhom" >
                                                    {msg => <div className="alert alert-danger">{msg}</div>}
                                                </ErrorMessage>
                                            </div>
                                            <button type="button" className="float-right btn btn-danger ml-2" data-dismiss="modal">Hủy</button>
                                            <button type="submit" className="float-right btn btn-success">
                                                {
                                                    infoModal.addMovie || infoModal.addUser ? 'Thêm' : 'Sửa'
                                                }
                                            </button>
                                        </Form>
                                    )}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = dispatch => {
    return {
        addUser: (user) => {
            dispatch(actionsUser.addUser(user));
        }
    }
}

export default connect(null, mapDispatchToProps)(Modal);